  
 
 body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

nav {
  background-color: #333;
  color: white;
  padding: 1rem;
}

nav ul {
  list-style-type: none;
  display: flex;
  gap: 20px;
}

h1, h2, h3 {
  color: #333;
}

select, button {
  margin: 10px 0;
  padding: 10px;
  font-size: 16px;
}
